import React, { useEffect } from 'react'
import styled from '@emotion/styled'
import bgHeader from '../assets/img/bg_header.png'
import AppNavBar from './AppNavBar'
import avatarDesign from '../assets/img/avatar_design.svg'
import Button from '@material-ui/core/Button';
import bottomGapWhite from '../assets/img/bottom_gap_white.svg'
import breakPoint from '../utils/breakPoint'
import "aos/dist/aos.css";
import AOS from 'aos';

function Header() {

    const HeaderContainer = styled.div`
        background-image: linear-gradient(to bottom, rgba(37, 41, 52, 0.9), rgba(37, 41, 52, 0.9)), url('${bgHeader}');
        background-repeat:no-repeat;
        background-size:cover;
        position: relative;
        .MuiPaper-elevation4{
            box-shadow: none;
        }
        .header__body{
            height: 110vh;
            display:grid;
            padding: 0 5%;
            place-content: center;
            grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
            grid-template-areas: "topLeft topRight"
                                 "bottomLeft bottomRight";
            gap: 16px;
            .header__info{
                color:white;
                align-self: center;
                h2{
                    text-align:left;
                    margin-bottom:4px;
                    font-weight: 700;
                    color: white;
                }
                .header-myname{
                    color: #E41D6F;
                }
                h1{
                    font-weight: 700;
                }
                .btn-download-cv{
                    display:inline-block;
                    text-decoration:none;
                    color: white;
                    border:2px solid white;
                    padding: 10px 24px;
                    margin-top: 32px;
                    border-radius: 30px;
                    &:hover{
                        background:white;
                        color: #E41D6F
                    }
                }
            }
            img{
                width:60%;
                margin: 0 auto;
            }
        }
        .bottom-gap-white{
            position:absolute;
            bottom:-1px;
            right:0;
            width:100%;
        }
        ${breakPoint('sm')}{
            .header__info{
                grid-area: bottomLeft !important;
                margin-top:16px;
            }
            img{
                grid-area: topLeft !important;
            }
        }

    `

    useEffect(()=>{
        AOS.init({
            duration : 600
        });
    },[])

    return (
        <HeaderContainer>
            <AppNavBar />
            <div className="header__body">
                <div className="header__info" data-aos="fade-up">
                    <h2>Hello, <span className="header-myname">my name Rian mandala</span></h2>
                    <h1>I'am a front-end web developer</h1>

                    <a href="https://drive.google.com/file/d/1CrEXdEZAOW9a-gy5BrakiSMnT3DLpNnD/view?usp=sharing" target="_blank" className="btn-download-cv">Download CV</a>
                </div>
                <img src={avatarDesign} alt="avatar design" data-aos="fade-up" />
            </div>
            <img className="bottom-gap-white" src={bottomGapWhite} alt="bottom gap white" />
        </HeaderContainer>
    )
}

export default Header
