import React,{useState, useEffect, useCallback} from 'react'
import styled from '@emotion/styled'
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import breakPoint from '../utils/breakPoint'

import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';
import PersonIcon from '@material-ui/icons/Person';
import EmojiObjectsIcon from '@material-ui/icons/EmojiObjects';
import ContactsIcon from '@material-ui/icons/Contacts';
import { useLocation } from 'react-router-dom'

const useStyles = makeStyles({
    list: {
      width: 250,
    },
    fullList: {
      width: 'auto',
    },
  });
function AppNavBar() {

    const classes = useStyles();
    const [openDrawer, setOpenDrawer] = useState(false) 

    const toggleDrawer = (open) => {
        setOpenDrawer(open);
    };

    const dataNavDrawer = [
        {Icon: PersonIcon, text: 'About',link:'#about'},
        {Icon: EmojiObjectsIcon, text: 'Projects', link: '#project'},
        {Icon: ContactsIcon, text: 'Contact', link: '#contact'},
    ]
    
      const list = (anchor) => (
        <div
          className={clsx(classes.list, {
            [classes.fullList]: anchor === 'top' || anchor === 'bottom',
          })}
          role="presentation"
          onClick={()=>toggleDrawer(false)}
          onKeyDown={()=>toggleDrawer(false)}
        >
          <List>
            { dataNavDrawer.map(({Icon, text, link}, index) => (
              <ListItem button key={text}>
                <a style={{display: 'flex',textDecoration: 'none', color: '#262A35'}} href={link}>
                  <ListItemIcon><Icon /></ListItemIcon>
                  <ListItemText primary={text} />
                </a>
              </ListItem>
            ))}
          </List>
        </div>
      );

    const {hash} = useLocation()

    const [scrollY, setScrollY] = useState(window.scrollY);

    const handleNavigation = useCallback(
      e => {
        const window = e.currentTarget;
        setScrollY(window.scrollY);
      }, [scrollY]
    );

    useEffect(() => {
      window.addEventListener("scroll", (e) => handleNavigation(e));
    
      return () => { // return a cleanup function to unregister our function since its gonna run multiple times
        window.removeEventListener("scroll", (e) => handleNavigation(e));
      };
    }, [scrollY]);

    const AppBarContainer = styled.div`
        .app-bar{
            transition: 0.5s;
            position:fixed;
            top:0;
            color: ${scrollY >=200 ? '#262A35' : 'white'};
            background:transparent;
            .MuiToolbar-root{
              padding: 0 5%;
            }
            .btn-nav:nth-of-type(1){
              margin-left:auto;
            }
            .btn-nav{
              padding: 10px 20px;
              color: ${scrollY >=200 ? '#262A35' : 'white'};
              text-decoration: none;
            }
            .btn-nav.active{
              position:relative;
              &:after{
                content: '';
                display: block;
                position: absolute;
                bottom: -4px;
                left: 20%;
                width: 38%;
                height: 3px;
                background: ${scrollY >=200 ? '#262A35' : '#E41D6F'};
              }
            }
            .btn-drawer{
                display:none;
            }

        }
        .app-bar.active{
          transition:0.5s;
          background: white;
        }
        ${breakPoint('sm')}{
            .btn-nav{
                display:none;
            }
            .btn-drawer{
                margin-left:auto;
                display:inline-flex !important;
            }
        }
    `

    return (
        <AppBarContainer>
            <AppBar position="sticky" className={`app-bar ${scrollY >=200 && 'active'}`}>
                <Toolbar>
                    <Typography variant="h6">
                        Rian Mandala
                    </Typography>
                    <a href="/#about" className={`btn-nav ${hash=="#about" && 'active'}`} color="inherit">About</a>
                    <a href="/#project" className={`btn-nav ${hash=="#project" && 'active'}`} color="inherit">Projects</a>
                    <a href="/#contact" className={`btn-nav ${hash=="#contact" && 'active'}`} color="inherit">Contact</a>
                    <IconButton onClick={()=>toggleDrawer(true)} className="btn-drawer" color="inherit" aria-label="menu">
                        <MenuIcon />
                    </IconButton>

                    <SwipeableDrawer
                        anchor="left"
                        open={openDrawer}
                        onClose={()=> toggleDrawer(false)}
                        onOpen={()=>toggleDrawer(true)}
                        >
                        {list("left")}
                    </SwipeableDrawer>

                </Toolbar>
            </AppBar>
        </AppBarContainer>
    )
}

export default AppNavBar
